import { useState } from 'react'
import userImg from '../../assets/images/download.jpg';
import {
    Form,
    Button
} from 'react-bootstrap';
import { Link } from 'react-router-dom';
import Copyright from '../../utils/Copyright';

const LockScreen = () => {
    const [password, setPassword] = useState("")
    const [formState, setFormState] = useState({
        errors: {}
    })

    const handleValidation = () => {
        let errors = {};
        let formIsValid = true;

        if (!password) {
            formIsValid = false;
            errors["password"] = "Password cannot be empty";
        }
        if (password.length < 8) {
            formIsValid = false;
            errors["password"] = "Password should be of 8 digit";
        }
        setFormState({ errors: errors });
        return formIsValid;
    }

    const handleSubmit = (e) => {
        e.preventDefault()
        if (handleValidation()) {
            console.log("form details are correct")
        }
    }
    return (
        <>
            <div className='container'>
                <div>
                    <img src={userImg} className="img-fluid w-20px" alt="" />
                    <div>
                        <h6>
                            Maria Laird
                        </h6>
                    </div>
                </div>
                <div>
                    <Form>
                        <Form.Group className="mb-3" controlId="formBasicPassword">
                            <Form.Label>Password</Form.Label>
                            <Form.Control type="password" placeholder="Enter password" value={password} onChange={e => setPassword(e.target.value)} />
                            <span style={{ color: "red" }}>{formState?.errors["password"]}</span>
                        </Form.Group>
                        <Button variant="primary" type="button" onClick={handleSubmit}>
                            Unlock
                        </Button>
                    </Form>
                </div>
                <div>
                    <h6>
                        <b>Not you? <Link to="/login">Sign up</Link> with a diffrent account</b>
                    </h6>
                </div>
                <Copyright />
            </div>
        </>
    )
}

export default LockScreen