import { useState } from 'react'
import {
    Form,
    Button
} from 'react-bootstrap';
import { Link } from 'react-router-dom';
import Copyright from '../../utils/Copyright';
import Logo from '../../assets/images/login-logo.png';
// import validator from 'validator';

const SignIn = () => {
    const [username, setUsername] = useState("")
    const [password, setPassword] = useState("")
    const [formState, setFormState] = useState({
        errors: {}
    })

    const handleValidation = () => {
        let errors = {};
        let formIsValid = true;

        if (!username) {
            formIsValid = false;
            errors["username"] = "Username cannot be empty";
        }
        if (!password) {
            formIsValid = false;
            errors["password"] = "Password cannot be empty";
        }
        if (password.length < 8) {
            formIsValid = false;
            errors["password"] = "Password should be of 8 digit";
        }
        setFormState({ errors: errors });
        return formIsValid;
    }

    const handleSubmit = (e) => {
        e.preventDefault()
        if (handleValidation()) {
            console.log("form details are correct")
        }
    }
    return (
        <>  

        <div class="signup">
            <div className='container'>
                <div className='signup-box'>
                    <div className='signup-form'>
                        <span className='position-relative'><img src={Logo} alt='' /><figure className='userimage position-absolute'></figure></span>
                    <div>
                        <Form>
                            <Form.Group className="mb-3" controlId="formBasicEmail">
                                <Form.Label>Username</Form.Label>
                                <Form.Control type="text" placeholder="Enter username" value={username} onChange={e => setUsername(e.target.value)} />
                                <span style={{ color: "red" }}>{formState?.errors["username"]}</span>
                            </Form.Group>

                            <Form.Group className="mb-3" controlId="formBasicPassword">
                                <Form.Label>Password</Form.Label>
                                <Form.Control type="password" placeholder="Enter password" value={password} onChange={e => setPassword(e.target.value)} />
                                <span style={{ color: "red" }}>{formState?.errors["password"]}</span>
                            </Form.Group>
                            <Form.Group className="mb-3" controlId="formBasicCheckbox">
                                <Form.Check type="checkbox" label="Remember me" />
                            </Form.Group>
                            <Button variant="primary" className='w-100 btn-black mt-1' type="button" onClick={handleSubmit}>
                                Login
                            </Button>
                            <h6 className='text-center already-content mt-3'>
                                Forgot your password?
                            </h6>
                        </Form>
                    </div>
            </div>
                <h6 className='text-center already-content mailrecieved-content mt-4'>
                        Didn't receive an email? <Link href="/signup">Resend</Link>
                </h6>
                <div className='text-center copyright-content'>  
                        <Copyright/>
                </div>
            </div>                
            </div>           
        </div>

        </>
    )
}

export default SignIn