import { useState } from 'react'
import {
    Badge,
    Form,
    Button
} from 'react-bootstrap';
import { Link } from 'react-router-dom';
import Copyright from '../../utils/Copyright';
import validator from 'validator';
import Logo from '../../assets/images/resetlogo.jpg';

const PasswordReset = () => {
    const [email, setEmail] = useState("")
    const [formState, setFormState] = useState({
        errors: {}
    })
    const handleValidation = () => {
        let errors = {};
        let formIsValid = true;

        if (!email) {
            formIsValid = false;
            errors["email"] = "Please enter email or phone information";
        }
        if ((!validator.isMobilePhone(email)) && (!validator.isEmail(email))) {
            formIsValid = false;
            errors["email"] = "Please enter valid details";
        }

        setFormState({ errors: errors });
        return formIsValid;
    }

    const handleSubmit = (e) => {
        e.preventDefault()
        if (handleValidation()) {
            console.log("form details are correct")
        }
    }
    return (
        <>
             <div class="signup">
                    <div className='container'>
                        <div className='signup-box'>
                            <div className='signup-form passreset-form'>
                            <span className='position-relative mb-5'><img src={Logo} alt='' /><figure className='userimage position-absolute'></figure></span>
                        <div className='mt-5 text-center'>
                            <Badge bg="warning" text="dark" className='mt-5 py-3 px-4 fw-normal yellow-badge'>
                                Complete field and instructions will be sent to you!
                            </Badge>{' '}
                        </div>
                            <div>
                                <Form>
                                    <Form.Group className="mb-3" controlId="formBasicEmail">
                                        <Form.Label>Email</Form.Label>
                                        <Form.Control type="text" placeholder="Enter Email or phone" value={email} onChange={e => setEmail(e.target.value)} />
                                        <span style={{ color: "red" }}>{formState?.errors["email"]}</span>
                                    </Form.Group>
                                </Form>
                                    <Button variant="dark" onClick={handleSubmit} className='btn-black fw-normal text-white ms-auto d-flex justify-content-end px-4 py-1 br-0 rounded-0 me-4 mb-4'>Reset</Button>{' '}
                                </div>
                                <div>
                                </div>
                            <div>                          
                        </div>
                        </div>
                             <h6 className='already-content mailrecieved-content mt-4 text-center'>
                                Remember your password? <Link to="/login">Sign in</Link>
                            </h6>
                            <div className='text-center copyright-content'>  
                                <Copyright/>
                            </div>
                    </div>
                        
                        <Copyright />
                    </div>
            </div>
        </>
    )
}

export default PasswordReset